import React from "react";
import { IAuthor, IPostSummary } from '../../interfaces';
import { getCategory, getAuthor, INestedCategory } from "../../tool";
import { PostPreview } from "./PostPreview";
import { EmptyContent } from "../Content/EmptyContent";



export class PostList extends React.Component<{posts: IPostSummary[], categories: INestedCategory[], authors: IAuthor[]}>
{
  render() 
  {
    return (
      <div>
      {
        this.props.posts.map((p,i) => (
          <div key={i}>
            <PostPreview post={p} categories={p.frontmatter.categories.map(c => getCategory(c, this.props.categories))} author={getAuthor(p.frontmatter.author, this.props.authors)}></PostPreview>
          </div>
        ))
      }
      {
        this.props.posts.length == 0 ? <EmptyContent/> : ""
      }
      </div>
    )
  }
}
