import { graphql, Link } from "gatsby";
import React from "react";
import { IQueryArrayResult, ICategory, IAuthor, IPost, ITag, IGatsbyLocation } from '../../interfaces';
import { ContentLayoutWithSidebar } from '../../components/Layout/ContentLayout';
import { SideBar } from "../../components/SideBar/SideBar";
import { nestCategories } from '../../tool';
import { SideBarQuickstart } from "../../components/SideBar/SideBarQuickstart";
import { SideBarHelp } from "../../components/SideBar/SideBarHelp";
import SideBarCategories from "../../components/SideBar/SideBarCategories";
import { PostList } from '../../components/Post/PostList';
import { IHeaderProps } from "../../components/Layout/Header";
import { config } from "../../config";
import Img from 'gatsby-image';
import { Link as LinkNoPrefetch } from '@reach/router';
import "./BlogPage.sass"

interface IQueryResult
{
  data: {
    posts: IQueryArrayResult<IPost>;
    categories: IQueryArrayResult<ICategory>
    authors: IQueryArrayResult<IAuthor>
    tags: IQueryArrayResult<ITag>
    pbi: any,
    ppv: any,
    pq: any
  };
  context: any;
  location: IGatsbyLocation;
}


export class Blog extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render(){
    let posts = this.props.data.posts.edges
      .map(e => e.node)
      .sort((a,b) => {
        if(a.fields.timestamp > b.fields.timestamp)
          return -1
        else
          return 1;
      });
    let categories            = nestCategories(this.props.data.categories.edges.map(e => e.node));
    let authors               = this.props.data.authors.edges.map(e => e.node);

    let header: IHeaderProps = {
      url: "blog",
      description: `Blog about Power BI, Power Pivot and Power Query  `,
      type: "object",
      title: `Blog Archive | ${config.site.title}`,
      tags: ["blog"]
    }

    return (
      <ContentLayoutWithSidebar
      header={header} 
      sidebar={(
        <SideBar isFixed={true}>
          <SideBarQuickstart></SideBarQuickstart>
          <SideBarHelp></SideBarHelp>
          <SideBarCategories></SideBarCategories>
        </SideBar>
      )} 
      >
      <div className="category-section">
        <LinkNoPrefetch to="/category/power-bi"  className="button action-button">
          <Img fixed={this.props.data.pbi.childImageSharp.fixed} style={{marginRight: "5px",  filter: "invert(1)"}}></Img>
          Power BI
        </LinkNoPrefetch>
        <LinkNoPrefetch to="/category/power-query" className="button action-button">
          <Img fixed={this.props.data.pq.childImageSharp.fixed} style={{marginRight: "5px",}}></Img>
          Power Query
        </LinkNoPrefetch>
        <LinkNoPrefetch to="/category/power-pivot" className="button action-button">
          <Img fixed={this.props.data.ppv.childImageSharp.fixed} style={{marginRight: "5px",  filter: "invert(1)"}}></Img>
          Power Pivot
        </LinkNoPrefetch>
      </div>
      <div style={{marginTop: "10px"}}>
        <PostList posts={posts} categories={categories} authors={authors}/>
      </div>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({pageContext, data, location}) => {
  return (
    <Blog data={data} location={location} context={pageContext}></Blog>
  );
};
export default Page;

export const pageQuery = graphql`
query posts($now: Int!) {
  posts: allMarkdownRemark(
    filter: {
      fileAbsolutePath: {
        regex: "/.*/data/posts/.*[.]md/"
      }, 
      fields: {
        publishTimestamp:{
          lt: $now
        }
      }
    }) {
    edges {
      node {
        ...postShort
      }
    }
  }
  pbi: file(
    absolutePath: { regex: "/.*\/data\/img\//"}  
    name:{ eq: "power-bi"}
  ){
    childImageSharp {
      fixed(width: 20, height: 20, grayscale: true) {
        ...GatsbyImageSharpFixed
      }
    }
  },
  ppv: file(
    absolutePath: { regex: "/.*\/data\/img\//"}  
    name:{ eq: "power-pivot"}
  ){
    childImageSharp {
      fixed(width: 20, height: 20, grayscale: true) {
        ...GatsbyImageSharpFixed
      }
    }
  }
  pq: file(
    absolutePath: { regex: "/.*\/data\/img\//"}  
    name:{ eq: "power-query"}
  ){
    childImageSharp {
      fixed(width: 20, height: 20, grayscale: true) {
        ...GatsbyImageSharpFixed
      }
    }
  }
  ...categories
  ...authorsMarkdown
  ...tags
}
`;
