import React from "react";
import { Link } from "gatsby";
import "./Post.sass";
import { FaClock, FaUser, FaEdit } from "react-icons/fa"
import * as moment from "moment";
import { INestedCategory } from '../../tool';
import { IAuthor, IPostSummaryFrontmatter } from '../../interfaces';

interface IProps
{
  author: IAuthor;
  commentCnt: number;
  isPreview?: boolean;
  frontmatter: IPostSummaryFrontmatter;
  categories: INestedCategory[];
}

export class PostHeader extends React.Component<IProps> {
  constructor(props) {
    super(props);
  }

  render() {
    const dateFormat = "DD. MMM YYYY"
    let date = moment(this.props.frontmatter.date);
    let update = null;
    if(this.props.frontmatter.updatedate != null && this.props.frontmatter.updatedate != "")
    {
      update = moment(this.props.frontmatter.updatedate);
      if(Math.abs(date.diff(update)) >= 86400000 || update.isAfter(date) == true)
      {
        update = moment(this.props.frontmatter.updatedate);
      }
    }

    return (
      <div className="post-header">
        <div className="post-meta">
          <div title="Author">
            <Link className="post-meta-data post-meta-content" to={"/author/"+this.props.author.frontmatter.slug}> 
              <FaUser/> 
              <span>{this.props.author != null ? this.props.author.frontmatter.firstname + " " + this.props.author.frontmatter.lastname : ""}</span>
            </Link>
          </div>
          {
            update != null ? <div title="Bearbeitet" className="post-meta-data post-meta-time">
              <FaEdit/>
              <span className="post-meta-content">{update.format(dateFormat)}</span> 
            </div> : ""
          }
          <div title="Angelegt" className=" post-meta-data post-meta-time">
            <FaClock/> 
            <span className="post-meta-content">{date.format(dateFormat)}</span> 
          </div>
        </div>
        <div className="post-subtitle">
        {
          this.props.frontmatter.subtitle != null ? <Link to={"/"+this.props.frontmatter.slug}>{this.props.frontmatter.subtitle}</Link> : ""
        }
        </div>
        <div>
          <h1>
            <Link to={"/"+this.props.frontmatter.slug}>{this.props.frontmatter.title}</Link>
          </h1>
        </div>
      </div>
    );
  }
};

