import React from "react";
import { Link } from "gatsby";
import "./Post.sass";
import { PostHeader } from './PostHeader';
import { HTMLContent } from "../Content/Content";
import { INestedCategory } from "../../tool";
import { IPostSummary, IAuthor } from "../../interfaces";

interface Props
{
  post: IPostSummary;
  categories: INestedCategory[];
  author: IAuthor;
}

export class PostPreview extends React.Component<Props> {
  constructor(props) {
    super(props);
  }

  render() {
    let { post } = this.props;
    return (
      <div className="post-preview">
        <div>
          <PostHeader frontmatter={post.frontmatter} commentCnt={post.fields.commentCnt} author={this.props.author} categories={this.props.categories}/>
        </div>
        <div className="post-content">
        <Link to={"/"+post.frontmatter.slug}>
          <HTMLContent content={this.props.post.fields.abstract ? this.props.post.fields.abstract : this.props.post.excerpt}> </HTMLContent>
        </Link>
          {/* <div>
            <Link to={post.frontmatter.slug}><span style={{verticalAlign: "middle"}}><FaLongArrowAltRight/></span> Mehr</Link>
          </div> */}
        </div>
      </div>
    );
  }
};

